import Vue from 'vue'
import Router from 'vue-router'
// import editor from '../page/editor/index'

const websiteIndex = resolve => require(['../page/index/index'], resolve)
const websiteApply = resolve => require(['../page/index/apply'], resolve)

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    
    {
      path: '/',
      name: 'websiteIndex',
      component: websiteIndex
    },
    {
      path: '/apply',
      name: 'websiteApply',
      component: websiteApply
    }
  ]
})



